/* ヘッダー ------------------------------*/
.l-header01 {
    z-index: 100;
    position: fixed;
    height: 80px;
    width: 100vw;
    // background-color: #d1cfcf;
}
/* ------------------------------*/

.l-header01__inner {
    z-index: 500;
    width: 1920px;
    height: 100%;
    // background: #a7a7a7;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: auto;
    padding-left: 15px;
    padding-right: 15px;
}
/* ------------------------------*/
/* ロゴ */

.l-logo-container {
    // background: #636161;
    width: 200px;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 20px;
}

.logo-box {
    width: 40px;
    height: 40px;
    margin-right: 15px;
    .logo-img {
        width: 100%;
        height: auto;
    }
    .logo-img:hover {
        opacity: 0.7;
        transition: 0.2s ease-in-out;
    }
}

.heading {
    font-size: 32px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: bold;
    color: #ffffff;
    letter-spacing: 4px;
}

/* ナビゲーション */
.l-header01__navi {
    // background: #636161;
    width: auto;
    margin-left: 120px;
}

.cp-header01__items {
    font-size: 16px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: bold;
    color: #ffffff;
    display: flex;
    list-style: none;
}
.cp-header01__item:not(:last-child) {
    margin-right: 30px;
}
.cp-header01__item a {
    display: inline-block;
    transition: 0.2s ease-in-out;
    color: #fff;
    text-decoration: none;
}
.cp-header01__item a:hover {
    opacity: 0.7;
}

.reservation-button {
    position: absolute;
    right: 20px;
    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 16px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: bold;
    color: #ffffff;
}

.reservation-button::before {
    content: "";
    display: inline-block;
    background-image: url("../../../../images/calendar.png");
    background-size: contain;
    background-repeat: no-repeat;
    width: 22px;
    height: 22px;
    transform: translate(-10px, 0px);
}
